<script>
  // collapse 展开折叠
  import CollapseTransition from 'element-ui/lib/transitions/collapse-transition';
  const menu = JSON.parse(localStorage.getItem('menu')).elementLists
  export default {
    components:{
      elCollapseTransition: CollapseTransition
    },
    data () {
      return {
        navData: [],
        info: {
          icon: '',
          name: ''
        },
        // 前一个页面的路由地址
        beforeRouter: this.$route.path
      }
    },
    methods: {
      // 获取左侧菜单数据，根据路由判断
      getNavData () {
        this.navData = []
        menu.forEach(e => {
          if (e.path.split('/')[1] === this.$route.path.split('/')[1]) {
            this.navData = e.children
            this.info = {
              icon: e.icon,
              name: e.name
            }
          }
        })
        console.log(this.navData)
        this.navData.forEach(e=>{
          if(e.children && e.children.length === 0){
            e.children = null
          }
          if(e.children){
            e.status = false
          }
        })
      },
      // 展现第三级菜单
      showNav(item,key){
        let arr = this.navData[key]
        arr.status = !arr.status
        this.navData.splice(key,1,arr)
      },
    },
    watch: {
      '$route': function () {
        // 如果没有切换一级菜单 则不改变二级菜单列表
        if(this.beforeRouter.split('/')[1] === this.$route.path.split('/')[1]){
          return
        }
        this.beforeRouter = this.$route.path
        this.getNavData()
      }
    },
    mounted () {
      this.getNavData()
    }
  }
</script>
